@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans&display=swap);
.global__container {
  width: 100vw;
  height: 100vh;
}

.main__container {
  width: 100%;
  display: flex;
  place-content: center;
  flex-direction: row;
}

.welcome__animation {
  width: 400px;
}

.login__card {
  margin-top: 20px;
  padding-top: 30px;
  margin-left: 10px;
  background-color: #393e46;
  width: 40%;
  height: 400px;
  /* box-shadow: 1px 1px grey; */
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.login__label {
  color: #ffd369;
  margin-top: 20px;
}

.login__heading {
  font-family: "Josefin Sans", sans-serif;

  font-size: 26px;
  color: white;
  text-transform: capitalize;
  margin-top: 40px;
  font-family: "Josefin Sans", sans-serif;

  /* display: flex;
  flex-direction: row; */
}

.email__icon {
  margin-right: 10px;
}

.login__input {
  padding: 6px 60px;
  font-family: "Josefin Sans", sans-serif;
  font-size: 18px;
  text-align: center;
  border: none;
  border-radius: 10px;
  margin-top: 10px;
}

.login__btn {
  margin-top: 40px;
  padding: 10px 60px;
  background-color: #ffd369;
  color: black;
  font-size: 16px;
  font-weight: bold;
  font-family: "Josefin Sans", sans-serif;
  /* border: none; */
  box-shadow: 1px 1px grey;
  border-radius: 10px;
  cursor: pointer;
}

.or__style {
  color: white;
  margin: 10px 0;
}

.create__btn {
  font-family: "Josefin Sans", sans-serif;
  background-color: rgb(57, 196, 57);
  color: #fff;
  font-size: 18px;
  padding: 10px 20px;
  border-radius: 10px;
  cursor: pointer;
}

@media (max-width: 800px) {
  .login__card {
    width: 60%;
  }
}

@media (max-width: 550px) {
  .login__card {
    width: 80%;
  }
  .login__input {
    padding: 6px 20px;
  }
}

