.navbar__logo {
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  color: black;
  text-transform: uppercase;
}

.navbar {
  width: 100vw;
  height: 60px;
  background-color: #ffd369;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  position: relative;
}
.navbar__heading {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  color: black;
  padding-right: 20px;
  font-weight: 400;
}

.navbar__btn {
  margin-left: 10px;
}

.navbar__menu {
  position: absolute;
  top: 0px;
  left: -200px;
  display: flex;
  flex-direction: column;
  background-color: #ffd369;
  /* background-color: crimson; */
  width: 200px;
  height: 100vh;
  transition: 850ms;
}

.navbar__active {
  left: 0;
  z-index: 1;
}

.cancel__btn {
  display: flex;
  width: 100%;
  justify-content: flex-end;
  padding-right: 20px;
  padding-top: 20px;
  cursor: pointer;
  transition: 850ms;
}

.menu__item {
  text-decoration: none;
  color: black;
  padding: 6px 0px;
  text-align: center;
  font-weight: bold;
  border-radius: 30px;
  width: 160px;
  margin: 20px auto;
  display: flex;
  justify-content: flex-start;
  align-items: baseline;
  transition: 850ms;
}
.menu__item:hover {
  background-color: #393e46;
  transition: 850ms;
  color: white;
}
.cancel__btn :hover {
  background-color: red;
  transition: 850ms;
}

.menu__icon {
  margin: 0 20px;
}

.nav_profile_image {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background-size: cover;
  align-self: center;
  border: none;
}

.nav_username {
  align-self: center;
  margin: 10px 0;
  color: #fff;
}

.nav_profile {
  background-color: #393e46;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #393e46;
  font-family: "Josefin Sans", sans-serif;
}

